.projects{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    padding-top: 10em;
}
.project-overlay, .projects-overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: var(--orange);
    z-index: 20;
}

.projects-content-top{
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.projects-content{
    width: 100%;
    padding: 0 8em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.projects-p{
    color: var(--font-light);
    width: 60%;
    font-size: 1.2rem;
    line-height: 1.5em;
    align-self: flex-end;
}
.projects-links{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-top: 5em;
}
.projects-link{
    text-decoration: none;
    position: relative;
    color: var(--blue-dark);
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 2em;
    background: rgb(243, 243, 243);
    transition: all 0.3s;
    border-radius: var(--div-radius);
}
.projects-link:hover{
    background: var(--orange);
}
.projects-link:hover p, small{
    color: var(--blue-dark);
}
.projects-link p{
    font-size: 2rem;
    width: max-content;
    margin: 0;
    color: var(--font-clr);
}
.projects-link small{
    position: absolute;
    right: 0;
    color: var(--orange);
}


/* Single project component */
.project{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: var(--bg);
    padding: 100vh 0 0 0;
}
.project-img-wrapper-cover{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
}
.project-img-wrapper-cover div{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: linear-gradient(0deg, rgba(0,0,17,1) 0%, rgba(0,0,13,0.8972631288843662) 37%, rgba(0,0,11,0.8132295154389881) 64%, rgba(0,0,6,0.47429394120929624) 82%, rgba(0,0,0,0) 100%);
}
.project-img-wrapper-cover img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-text{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    gap: 3em;
    padding: 4em 0;
}
.project h1{
    width: 80%;
    font-size: 8rem;
    line-height: 0.9em;
    color: white;
    text-align: center;
}
.project-desc, .project-sub{
    color: white;
    width: 60%;
    text-align: center;
    line-height: 1.5em;
}
.project small{
    color: var(--orange);
}


.project-imgs-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 4em;
    padding: 4em 8em;
}
.project-video-wrapper{
    width: 100%;
    border-radius: var(--div-radius);
    overflow: hidden;
}
.project-video-wrapper video{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: var(--radius);
}
.project-imgs-wrapper div{
    width: 100%;
    border-radius: var(--div-radius);
    overflow: hidden;
}
.project-imgs-wrapper div img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.project-imgs-text p{
    line-height: 1.5em;
}


.project-buttons{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 4em 0;
}

.project-fallback{
    width: 100%;
    height: 5px;
    background: rgb(0, 0, 0);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .projects-content{
        padding: 2em;
    }
    .projects h1, .projects-p, .project h1, .project-desc{
        width: 100%;
        text-align: center;
    }
}
@media only screen and (max-width: 480px){
    .projects-content{
        padding: 1em;
    }
    .projects-content h1{
        text-align: left;
    }

    .project h1, .project-desc{
        width: 100%;
        text-align: center;
    }
    .projects-p{
        width: 100%;
        text-align: left;
        font-size: 19px;
        line-height: 27px;
    }
    .projects h1, .project h1{
        font-size: 4rem;
    }
    .projects-links{
        width: 100%;
    }
    .projects-link p{
        font-size: 1.2rem;
        text-align: left;
    }
    

    .project{
        padding-top: 100vh;
    }
    .project-text{
        padding: 4em 1em;
    }
    .project-video-wrapper, .project-img-wrapper div{
        width: 100%;
        height: 350px;
    }
    .project-imgs-wrapper{
        padding: 1em;
    }
    .project-prev-btn, .project-next-btn{
        font-size: 1rem;
    }
    .project-imgs-wrapper div{
        width: 100%;
        height: 350px;
    }
}
@media screen and (min-width: 1440px) {
    .projects{
        padding-top: 16em;
    }
    .projects-p{
        font-size: 2rem;
    }

    .projects-content{
        padding: 0 16em;
    }
    .projects-link p{
        font-size: 2.5rem;
    }
    .projects-link small{
        font-size: 1.5rem;
    }

    .project-desc, .project-sub{
        font-size: 2rem;
    }
}